.MentalMapper .Relationship {
  position: absolute;
  cursor: pointer;
  z-index: 0;
}
.MentalMapper .Relationship--excluded-by-filter {
  opacity: 0.15;
}
.MentalMapper .Relationship--temp {
  z-index: 2;
}
.MentalMapper .Relationship__svg {
  overflow: visible !important;
  position: absolute;
  pointer-events: none;
}
.MentalMapper .Relationship__svg--hit path,
.MentalMapper .Relationship__svg--hit line {
  pointer-events: auto;
}
.MentalMapper .Relationship--temp-line {
  position: fixed;
  top: 0;
  left: 0;
}
.MentalMapper .Relationship--has-temp-relationship {
  pointer-events: none;
}
.MentalMapper .Relationship--has-temp-relationship > .Relationship__svg--hit > line {
  pointer-events: none;
}
.MentalMapper .Relationship__svg-arrow--positive {
  fill: #0351A6;
}
.MentalMapper .Relationship__svg-arrow--negative {
  fill: #BF5513;
}
.MentalMapper .Relationship__svg-arrow--neutral {
  fill: #333;
}



